.rightSideNav{
    position: fixed;
    top:0;
    right:0;
    width:350px;
    height:85%;
    background: #F6F5FD 0% 0% no-repeat padding-box;
    border-radius: 0px 0px 0px 70px;
   
}



.category{
    margin-left: 10px;
    text-align: left;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 16px;
    font-weight: bolder;
    color:#4303D5;
}
.subCategory{
    margin-left: 10px;
    text-align: left;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 12px;
    color: black;
    font-style: oblique;
}
.serviceName{

    margin-left: 20px;
    font-family:  Helvetica;
    font-size: 14px;
    color: black;
    font-weight: bolder;
}

.RightNavServiceImg{
    padding-top: 1rem;
    width: 60px;
    height: 60px;
    border-radius: 50%;
    text-align: center;
    background: transparent linear-gradient(180deg, #4303D6 0%, #5E03AB 100%) 0% 0% no-repeat padding-box;
    box-shadow: 0px 3px 6px #00000029;
    border-radius: 48px;
    opacity: 1;
    margin-left: 10px;
    
}

.more{
    text-align: right;
    font-family:  Helvetica;
    font-size: 16px;
    color: black;
    font-weight: bolder;
}