.login-form{
  top: -0.5px;
  left: 189px;  
  background: transparent url('../img/Background.png') 0% 0% no-repeat padding-box;
  opacity: 1;
  background-position: center center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;  
  position: fixed;
  border-radius: 50px 50px 50px 50px;
  
  }

  .insidebox{
    margin-top: 6rem;
    margin-left: -5rem;
    width: 268px !important;
    height: 457px;
    background: #F6F5FD 0% 0% no-repeat padding-box;
  
    border-radius: 60px;
    opacity: 1;
  }

  .login-logoImg{
    margin-left: 1.5rem;
    margin-top: 2rem;
    Right:0;
    Top:0;
  
    display: inline-block;
    width: 189px;
    height: 128px;
    background: url('../img/LoginLogo.png') 0% 0% no-repeat padding-box;
  
  }

  .login-title{
    margin-top:1.5rem;
    display: inline-block;
    text-align: right;
    margin-right: 4rem;
    right:0;
    top:0;
  }

  .login-titleName{
    font-family: Helvetica;
    font-weight:bolder;
    font-size: 26px;
  }

  .personWithDoor{
    margin-left: 6rem;
  }

  .login-submit{
    background: transparent linear-gradient(180deg, #4B03C7 0%, #5C03AC 100%) 0% 0% no-repeat padding-box;
    box-shadow: 4px 3px 8px #00000029 !important;
    border-radius: 7px !important;
    width: 20rem !important;
    height: 3rem !important;
    opacity: 1;   
    margin-left: 6rem !important;
    margin-top: 1rem !important;
    
  }

  .submitLabel{
    color:white !important;
    font-family: Helvetica !important;
    font-size: 18px !important;
    opacity: 1;
    display: inline-block !important;
    border-radius: 1px !important;
  }

  .emailBox{
    margin-left: 6rem !important;
    width: 20rem !important;
    background-color: #ffffff !important;
    border-radius: 5px;
  }

  .passwordBox{
    margin-left: 6rem !important;
    width: 20rem !important;
    background-color: #ffffff !important;
    border-radius: 5px;
  }

  .saveCheckBox{
    margin-left: 6rem !important;
  }