.installation-logoImg{
  margin-left: 10px;
  margin-top: 10px;
  Right:0;
  Top:0;
  display: inline-block;
  height: 58px;
  background: url('../img/Logo.png') 0% 0% no-repeat padding-box;
  
  }

  .installation-biggerbox{
    height: 100%;
    background: #FFFFFF 0% 0% no-repeat padding-box;
    border: 1px solid #707070;
    border-radius: 23px;
    opacity: 1;
    display: inline-block;
  }
  
  .acceptanceButton{
    margin-top: -2rem !important;
    background: #FFFFFF 0% 0% no-repeat padding-box;
    box-shadow: 4px 3px 8px #00000029;
    border: 1px solid #707070;
    border-radius: 14px;
    opacity: 1;
    display: inline-block;
  }

  .insideAcceptanceButton{
    background: transparent linear-gradient(180deg, #4B03C7 0%, #5C03AC 100%) 0% 0% no-repeat padding-box;
    box-shadow: 4px 3px 8px #00000029;
    border-radius: 14px !important;
    opacity: 1;
  }

  .accept{
    color:white;
    font-family: Helvetica;
    font-size: 1.5vw;
    opacity: 1;
    text-align: center!important;
    margin-left: 0.8rem !important;
  }

  .decline{
    color:Black;
    font-family: Helvetica;
    font-size: 1.5vw;
    opacity: 1;
    text-align: center!important;
    margin-left: 0.4rem !important;
  }
  .alternate-acceptanceButton{
    margin-top: -2rem !important;
    background: #FFFFFF 0% 0% no-repeat padding-box;
    box-shadow: 4px 3px 8px #00000029;
    border: 1px solid #707070 !important;
    border-radius: 14px;
    opacity: 1;
    display: inline-block;
  }
  .alternate-insideDeclineButton{
    right:0px !important;
    background: #FFFFFF 0% 0% no-repeat padding-box;
    box-shadow: 4px 3px 8px #00000029 !important; 
    border: 1px solid #707070!important;
    border-radius: 14px !important; 
    opacity: 1;
    }
  .alternate-insideAcceptanceButton{

    color:black !important; 
  }
  .alternate-accept{
    margin-left: -0.4rem;
    display:inline-block;
    color:#5C03AC;
    font-family: Helvetica;
    font-size: 1.5vw;
    opacity: 1;
  }
  .alternate-decline{

    color:Black;
    font-family: Helvetica;
    font-size: 1.5vw;
    opacity: 1;
  }

  .install-proceedButton{
    background: #A2A2A2 0% 0% no-repeat padding-box !important;
    box-shadow: 4px 3px 8px #00000029;
    border-radius: 14px!important;      
    opacity: 1;     
  }
  .proceedLabel{
    margin-top: 0.4rem;
    color:white;
    font-family: Helvetica;
    font-size: 16px;
    opacity: 1;
    font-weight: 600;
    display: inline-block;  
  }
  .proceedButtonAfterAccept{
    background: transparent linear-gradient(180deg, #4B03C7 0%, #5C03AC 100%) 0% 0% no-repeat padding-box !important;
    box-shadow: 4px 3px 8px #00000029;
    border-radius: 14px!important;      
    opacity: 1;     
  }
  .install-tick{
    margin-top: -31.5px;
    margin-left: 0px;
    text-align: center;
    top: 10px;
    height:12px !important;
    width:12px !important;
  }
  .moreDetail{
    right:0 !important;
    bottom:0 !important;
    margin-bottom: 2rem;
    font: Bold 18px/28px Helvetica;
    letter-spacing: 0px;
    color: #4303D5;
    opacity: 1;
  }