  /***************Gears************************/
  .annimated-box{
    width: 220px !important;
    display: block;
    height: 100%;
    position: relative;
    min-height: 320px;
  }
  @keyframes ckw {
    0% {
        transform: rotate(0deg);
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
        transform: rotate(360deg);
    }
}
@keyframes ckw {
    0% {
        transform: rotate(0deg);
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
        transform: rotate(360deg);
    }
}
@keyframes cckw {
    0% {
        transform: rotate(360deg);
        transform: rotate(360deg);
    }
    100% {
        transform: rotate(0deg);
        transform: rotate(0deg);
    }
}
@keyframes cckw {
    0% {
       transform: rotate(360deg);
        transform: rotate(360deg);
    }
    100% {
        transform: rotate(0deg);
        transform: rotate(0deg);
    }
}

