
.whitebox{
    background: #FFFFFF 0% 0% no-repeat padding-box;
    border: 1px solid #707070;
    opacity: 1;
    height: 180px;
    border-radius: 43px;
}
.coloredBox{
    background: transparent linear-gradient(180deg, #5103BE 0%, #5D03AB 100%) 0% 0% no-repeat padding-box;
    border: 1px solid #707070;
    opacity: 1;
    height: 180px;
    border-radius: 43px;
}

.insideColoredBox{
    margin-top: 4rem !important;
    text-align: center;
    font-family: Helvetica;
    letter-spacing: 0;
    color: #FFFFFF;
    justify-content: space-between;
    height: 150px;
    font-size: 1.1rem;
}

    
    
.newInstance-containerServices{
    display: flex;
    text-align: center;
    border-top: 2px solid  #FFFFFF;
    background: 0% 0% no-repeat padding-box;
    padding-bottom: 1rem;
    margin-left: 3rem !important;
    margin-top:10px;  
}

.newInstance-services1{
       
    font-weight: bold;
    background-color: #FFFFFF;
    box-shadow: 0px 3px 6px #00000029;
    line-height: 40px;
    text-align: center;
    border-radius: 50%;
    display:inline-block;
    border:2px solid #5D03AB; ;
    text-align: "center";
    font-family: Arial, Helvetica, sans-serif;
    color: #5D03AB;
    font-weight: bold !important;
    width: 60px;
    height: 60px;
    line-height: 25px;
    font-size:11px;
 
  }

  .newInstance-containerLine{   
    justify-content: space-between;
    border-top: 4px solid  #B1B8C2;
    background: 0% 0% no-repeat padding-box; 
    height: 4px;
    margin-left: 5rem;
  }
  .newInstance-containerLine div {
    font-weight: bold;
    background: #919191 0% 0% no-repeat padding-box;
    text-align: center;
    border-radius: 50%;
    margin-top: -15px;
    width: 27px;
    height: 27px;
    line-height: 45px;
  }  
    
  #newInstance-first-tick-mark {
    background: transparent linear-gradient(180deg, #4303D6 0%, #5E03AB 100%) 0% 0% no-repeat padding-box;
  }

    
  
  .newInstance-containerLabel{
    display: flex;
    text-align: center;
    padding-top: 1rem;
    background: 0% 0% no-repeat padding-box;
    margin-left: 3rem;
  }
  .newInstance-containerLabel span{
    font-family : Helvetica;
    color:#919191;
    letter-spacing: 0;
    text-align: center;
    font-weight: bolder !important;
    font-size: 13px;
  }